<template>
  <div v-if="!product || product.length === 0">
    <p class="gioHangTrong">Cha c lch s</p>
  </div>
  <div v-else class="table-container">
    <table>
      <thead>
        <tr>
          <th>STT</th>
          <th>Ngy Gi t Hng</th>
          <th>Hnh</th>
          <th>Tn Sn Phm</th>
          <th>n Gi</th>
          <th>Size</th>
          <th>S Lng</th>
          <th>Tng Tin</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(order, orderIndex) in product" :key="orderIndex">
          <!-- Hng tiu  n hng -->
          <tr class="order-header">
            <td colspan="8">n Hng Th {{ orderIndex + 1 }}</td>
          </tr>

          <!-- Sn phm trong n hng -->
          <tr v-for="(item, itemIndex) in order.items" :key="item.id">
            <td>{{ itemIndex + 1 }}</td>
            <td>{{ order.dateTime }}</td>
            <td>
              <img :src="item.image" alt="Product Image" class="product-img" />
            </td>
            <td>{{ item.name }}</td>
            <td>{{ item.price.toLocaleString() }},000 VN</td>
            <td>{{ item.size }}</td>
            <td>{{ item.quantity }}</td>
            <td>{{ item.totalPrice.toLocaleString() }},000 VN</td>
          </tr>

          <!-- Thng tin ngi t hng -->
          <tr class="order-user-info">
            <td colspan="8">
              <strong>Ngi t hng:</strong> {{ order.customerName }}<br />
              <strong>a ch:</strong> {{ order.customerAddress }}<br />
              <strong>S in thoi:</strong> {{ order.customerPhone }}
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </div>
</template>


<script>
export default {
  data() {
    return {
      // product: JSON.parse(localStorage.getItem("productCreate")) || null,
      // orderHistory: [],
      product: null,
    };
  },
  created() {
    const currentUser = JSON.parse(localStorage.getItem("currentUser"));
    if (currentUser) {
      const productHistory = JSON.parse(localStorage.getItem("orderHistory")) || {};
      this.product = productHistory[currentUser.username] || [];
    }
  },
};
</script>

<style scoped>
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 7%;
}

table th,
table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

table th {
  background-color: #f2f2f2;
}

.table-container {
  counter-reset: rowNumber;
}

.rowNumber::before {
  counter-increment: rowNumber;
  content: counter(rowNumber);
}

.product-img {
  width: 50px;
  height: 50px;
  object-fit: cover;
}
</style>