<template>
  <div class="checkout-container">
    <h2>Thng Tin t Hng</h2>
    <form @submit.prevent="submitOrder">
      <div class="form-group">
        <label for="name">Tn ngi t hng:</label>
        <input type="text" v-model="formData.name" id="name" required />
      </div>

      <div class="form-group">
        <label for="address">a ch:</label>
        <input type="text" v-model="formData.address" id="address" required />
      </div>

      <div class="form-group">
        <label for="phone">S in thoi:</label>
        <input type="tel" v-model="formData.phone" id="phone" required />
      </div>

      <div class="form-group">
        <label for="payment">Phng thc thanh ton:</label>
        <select v-model="formData.paymentMethod" id="payment">
          <option value="cash">Thanh ton khi nhn hng</option>
          <option value="credit">Th tn dng</option>
          <option value="momo">Momo</option>
        </select>
      </div>
      <h5>*Lu : n hng s c t 3 - 4 ngy.</h5>
      <h3 class="totalMoney">Tng tin: {{ tongtien }},000 VN</h3>

      <button @click="buyNow" type="submit" class="payment-button">
        Thanh Ton
      </button>
    </form>
  </div>
</template>


<script>
import cart from "../data/cart";
import purchasedProduct from "../data/purchasedProducts";
export default {
  data() {
    return {
      formData: {
        name: "",
        address: "",
        phone: "",
        paymentMethod: "cash", // Gi tr mc nh
      },
      cart: cart,
      purchasedProduct: purchasedProduct,
    };
  },
  computed: {
    tongtien() {
      return this.cart.reduce(
        (total, item) => total + item.quantity * item.price,
        0
      );
    },
  },
  methods: {
    submitOrder() {
      const currentUser = JSON.parse(localStorage.getItem("currentUser"));
      if (!currentUser) {
        alert("Vui lng ng nhp  tip tc thanh ton!");
        this.$router.push("/comLogin");
        return;
      }

      // Ly danh sch n hng t localStorage
      const productHistory =
        JSON.parse(localStorage.getItem("orderHistory")) || {};
      const userOrderHistory = productHistory[currentUser.username] || [];

      // To n hng mi
      // To n hng mi
      const newOrder = {
        dateTime: new Date().toLocaleString("vi-VN"), // Thi gian hin ti
        customerName: this.formData.name,
        customerAddress: this.formData.address,
        customerPhone: this.formData.phone,
        items: this.cart.map((item) => ({
          id: item.id,
          image: item.image,
          name: item.name,
          price: item.price,
          size: item.size,
          quantity: item.quantity,
          totalPrice: item.price * item.quantity,
        })),
      };

      // Lu n hng mi vo lch s
      userOrderHistory.push(newOrder);
      productHistory[currentUser.username] = userOrderHistory;

      // Cp nht vo localStorage
      localStorage.setItem("orderHistory", JSON.stringify(productHistory));

      // Xa gi hng sau khi thanh ton
      this.cart.length = 0;

      alert("Thanh ton thnh cng!");
      this.$router.push("/orderHistory");
    },
  },
};
</script>

<style>
.checkout-container {
  width: 600px;
  height: 450px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f8f8f8;
  margin-top: 7%;
}

h2 {
  margin-bottom: 20px;
  color: #333;
}

.form-group {
  width: 450px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

label {
  width: 150px;
  /* t chiu rng c nh cho label */
  margin-right: 10px;
  font-weight: bold;
  color: #555;
  text-align: right;
}

input[type="text"],
input[type="tel"],
select {
  width: calc(100% - 170px);
  /* Tr i chiu rng ca label v margin-right */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
}

button.payment-button {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  color: white;
  background-color: #28a745;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button.payment-button:hover {
  background-color: #218838;
}
.totalMoney {
  float: right;
}
</style>