<template>
  <div class="background-Ao">
    <img src="@/assets/aoSoMi/backgroundAoSoMi.png" alt="" />
  </div>
  <div class="jus"></div>
  <div class="product-grid">
    <div v-for="cata in filterProduct" :key="cata.id" class="image-container">
      <router-link
        :to="{ name: 'ProductDetailAoSoMi', params: { id: cata.id } }"
        class="no-underline"
      >
        <img :src="cata.image" alt="Tn hnh nh" />
        <h3>{{ cata.name }}</h3>
        <p>{{ cata.price }},000 VN</p>
        <div class="status">
          <p v-if="cata.quality == 0">Ht hng</p>
          <p v-else>{{ cata.status }}</p>
        </div>
      </router-link>
    </div>
  </div>
  <!-- Phn phn trang -->
  <div class="pagination-buttons">
    <button
      v-for="page in totalPage"
      :key="page"
      :class="{ active: page === currentPage }"
      @click="changePage(page)"
    >
      {{ page }}
    </button>
  </div>
  <div class="introduction-AoThun">
    <p>
      HAPPYSTORE cung cp cc mu o s mi nam cht liu cao cp, thit k lch
      lm, a dng kiu dng nh tay ngn, tay di c tr, form rng, ph hp
      mi hon cnh.
    </p>
    <hr />
    <h1 style="text-align: center">
      <strong>o S Mi Nam Do Ph, Cng S Thng Hiu HAPPYSTORE</strong>
    </h1>
    <p>
      o s mi - biu tng thi trang trng tn theo thi gian, l item khng
      th thiu trong mn  ca bt k ai. D bn l ai, theo ui phong cch
      no, o s mi cng c th gip th hin bn thn mt cch hon ho nht.
      Hiu c iu , <strong>HAPPYSTORE </strong>lun n lc pht trin
      nhng sn phm cht lng, a dng phong cch, p ng mi nhu cu.
    </p>
    <h2><strong>Top nhng mu o s mi nam HOT nht ti HAPPYSTORE</strong></h2>
    <p>
      Ti HAPPYSTORE, o s mi nam lun l items c phi mnh sn n nhiu
      nht. Di y l mt s mu o s mi nam p thnh hnh nht ti
      HAPPYSTORE m bn khng nn b qua:
    </p>
    <h2><strong>Hng dn chn size o s mi nam chun nht</strong></h2>
    <p>
      La chn size o s mi nam chun l cha kha  tn ln vc dng v phong
      cch ca bn. Sau y l b quyt "chun chnh" gip bn t tin la chn
      o s mi ph hp:
    </p>
    <ul>
      <li>Da vo s o ca c th  chn size o ph hp.</li>
      <li>
        Nn chn o c  rng va phi  thoi mi c ng. Khng nn chn o
        qu m s khin bn kh chu, l khuyt im hoc o qu rng khin bn
        trng lum thum.
      </li>
      <li>
        Nn th o trc khi mua  m bo chic o va vn vi c th. Hy th
        vi qun ty s phi cng  chc chn c c mt set  ph hp.
      </li>
      <li>
        Ngoi ra, bn cng c th tham kho bng size chun ca HAPPYSTORE di
        y  d dng la chn.
      </li>
    </ul>
    <p class="bangSizeAoSoMi">
      <img src="@/assets/aoSoMi/bangSizeAoSoMi.jpg" alt="" />
    </p>
  </div>
</template>

<script>
import item from "../data/itemsSoMi";
export default {
  name: "ProductSoMi",
  data() {
    return {
      products: item,
      currentPage: 1,
      limit: 8,
    };
  },
  computed: {
    totalPage() {
      return Math.ceil(this.products.length / this.limit);
    },
    filterProduct() {
      const start = (this.currentPage - 1) * this.limit;
      const end = start + this.limit;
      return this.products.slice(start, end);
    },
  },
  methods: {
    changePage(page) {
      if (page >= 1 && page <= this.totalPage) {
        this.currentPage = page;
        window.scrollTo({
          top: 0,
          behavior: "smooth", // To hiu ng cun mt m
        });
      }
    },
  },
};
</script>

<style>
.bangSizeAoSoMi img {
  width: 95%;
}
</style>
