<template>
  <div class="main-CTSP">
    <div class="container-CTSP">
      <img :src="product.image" alt="Hnh nh sn phm" />
      <div class="info-CTSP">
        <h1>{{ product.name }}</h1>
        <p class="description-CTSP">{{ product.description }}</p>
        <div class="info-details">
          <p class="price-CTSP">{{ product.price }},000 VND</p>
          <p v-if="product.quality == 0" class="status-CTSP">Ht hng</p>
          <p v-else class="status-CTSP">{{ product.status }}</p>
        </div>
        <div class="size-selection">
          <span>Kch thc:</span>
          <label v-for="size in product.size" :key="size">
            <input
              type="radio"
              id="{{ 'size-' + size }}"
              name="size"
              :value="size"
              v-model="selectedSize"
            />
            {{ size }}
          </label>
        </div>
        <div v-if="product.quality == 0">Ht hng</div>
        <div v-else>
          <button @click="addToCart(product)" class="add-to-cart">
            Thm vo gi hng
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import cart from "@/data/cart";
import ProductAoThun from "@/data/itemsAoThun";

export default {
  data() {
    return {
      product: {},
      cart: cart,
      selectedSize: "S",
    };
  },
  mounted() {
    // Ly id t url v
    let id = this.$route.params.id;
    // T id tm trong database thng tin ca id 
    this.product = ProductAoThun.find((x) => x.id == id);
  },
  methods: {
    addToCart(product) {
      // Kim tra nu sn phm vi cng kch thc  c trong gi hng
      const infoProduct = this.cart.find((item) => item.id == product.id && item.size == this.selectedSize && item.category == "AoThun");
      if (infoProduct) {
        if (infoProduct.quantity < product.quality) {
          infoProduct.quantity += 1;
        }
      } else {
        if (product.quality > 0) {
          //To i tng
          const newProduct = { ...product, quantity: 1, size: this.selectedSize };
          this.cart.push(newProduct);
        }
      }
      product.quality--;
        
    },
  },
};
</script>

