<template>
  <div class="main-ComCart">
    <div v-if="this.cart.length == 0">
      <p   class="gioHangTrong">Gi Hng Trng</p>
    </div>
    <div v-else class="modal-content">
      <h3 class="title-gioHang">Gi hng ca bn:</h3>
      <hr />
      <table style="width: 100%" class="text-center table">
        <tr>
          <th></th>
          <th>HNH</th>
          <th>SN PHM</th>
          <th>n gi</th>
          <th>Size</th>
          <th>S lng</th>
          <th>Tin</th>
          <th></th>
        </tr>
        <!-- dng v-for th hin gi hng -->
        <tr v-for="item in cart" :key="item.id">
          <td class="align-middle">
            <button @click="removeCart(item.id)" class="btn btn-danger">X</button>
          </td>
          <td><img :src="item.image" style="height: 100px; width: 100px" /></td>
          <td class="align-middle">{{ item.name.slice(0, 15) }}</td>
          <td class="align-middle">{{ item.price }},000 VN</td>
          <th>{{ item.size }}</th>
          <td class="align-middle">
            <button @click="decreaseQuantity(item)">-</button>
            {{ item.quantity }}
            <button @click="increaseQuantity(item)">+</button>
          </td>
          <td class="align-middle">{{ item.price * item.quantity }},000 VN</td>
        </tr>

        <tr>
          <th>
            <button @click="removeAll()" class="btn btn-danger">
              Xa ht!
            </button>
          </th>
          <th></th>
          <th></th>
          <th></th>
          <th>Tng s lng: {{ tongsoluong }}</th>
          <th>Tng tin: {{ tongtien }},000 VN</th>
          <th>
            <button @click="kiemTraDangNhap" class="btn btn-primary">
              Mua hng
            </button>
          </th>
        </tr>
      </table>
      <hr />
    </div>
  </div>
</template>

<script>
import items from "../data/itemsProductHome";
import items1 from "../data/itemsAoThun";
import items2 from "../data/itemsSoMi";
import items3 from "../data/itemsQuanJean";
import items4 from "../data/itemsQuanShort";
import cart from "../data/cart";

export default {
  data() {
    return {
      cart: cart,
    };
  },
  computed: {
    tongtien() {
      return this.cart.reduce(
        (total, item) => total + item.quantity * item.price,
        0
      );
    },
    tongsoluong() {
      return this.cart.reduce((total, item) => total + item.quantity, 0);
    },
  },
  methods: {
    removeCart(id) {
      const index = this.cart.findIndex((item) => item.id == id);
      this.cart.splice(index, 1);
    },
    removeAll() {
      this.cart.splice(0, this.cart.length); // Lm rng mng gi hng
    },
    increaseQuantity(item) {
      const stockItem = (() => {
        switch (item.category) {
          case "home":
            return items.find((x) => x.id === item.id);
          case "AoThun":
            return items1.find((x) => x.id === item.id);
          case "AoSoMi":
            return items2.find((x) => x.id === item.id);
          case "QuanJean":
            return items3.find((x) => x.id === item.id);
          case "QuanShort":
            return items4.find((x) => x.id === item.id);
          default:
            return null; // Nu khng tm thy loi sn phm no ph hp
        }
      })();

      if (stockItem.quality > 0) {
        item.quantity++;
        stockItem.quality--; // Gim s lng hng trong kho
      }
    },
    decreaseQuantity(item) {
      const stockItem = (() => {
        switch (item.category) {
          case "home":
            return items.find((x) => x.id === item.id);
          case "Aothun":
            return items1.find((x) => x.id === item.id);
          case "AoSoMi":
            return items2.find((x) => x.id === item.id);
          case "Quanjean":
            return items3.find((x) => x.id === item.id);
          case "QuanShort":
            return items4.find((x) => x.id === item.id);
          default:
            return null; // Nu khng tm thy loi sn phm no ph hp
        }
      })();
      if (item.quantity > 1) {
        item.quantity--;
        stockItem.quality++;
      } else {
        item.quantity == 1;
        return;
      }
    },
    kiemTraDangNhap() {
      const currentUser = JSON.parse(localStorage.getItem("currentUser"));
      if (!currentUser) {
        alert("Vui lng ng nhp  tip tc thanh ton!");
        this.$router.push("/comLogin"); // iu hng n trang ng nhp
        return; // Kt thc phng thc  khng cho php thanh ton
      } else {
        this.$router.push("/comPayment");
      }
    },
  },
};
</script>

<style>
.main-ComCart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 7%;
  width: 90%; /* Gii hn chiu rng  gi ni dung trong mt khung */
  max-width: 1200px; /* Gii hn chiu rng ti a */
  margin: auto; /*  cn gia ni dung nu c chiu rng */
  padding: 20px;
  background-color: #f9f9f9; /* Thm mu nn nh */
  border: 1px solid #ddd;
  border-radius: 10px;
}

.gioHangTrong {
  font-size: 30px;
  border: solid 1px;
  color: rgb(70, 123, 154);
}
.title-gioHang {
  margin-right: 800px;
}
.btn-primary {
  width: 150px;
  height: 40px;
  background-color: #91b0cf;
  color: white;
  border-radius: 5px;
}

.btn-primary:hover {
  background-color: #abbfd4;
  cursor: pointer;
}
.modal-content {
  width: 100%;
  border-collapse: collapse; /* Loi b khong cch gia cc ng vin */
}
</style>